import { Text, StyleSheet, Image, View } from '@react-pdf/renderer';
import CvTitle from '../cv-title/cv-title.component';

const ContactItem = ({ src, info }) => (
  <View style={styles.contactItem}>
    <View style={styles.iconContainer}>
      <Image style={styles.icon} src={src} />
    </View>
    <Text style={styles.contactInfo}>{info}</Text>
  </View>
);

const CvProfile = () => (
  <View style={styles.contactContainer}>
    <View style={styles.avatarContainer}>
      <View style={styles.avatar}>
        <Image
          style={styles.image}
          src="data:image/jpeg;base64,"
        />
      </View>
      <View style={styles.name}>
        <Text>Lê Đình Khánh</Text>
      </View>
      <View
        style={{
          fontSize: 12,
          marginBottom: 10,
          opacity: 0.6,
        }}
      >
        <Text>Front-end Developer</Text>
      </View>
    </View>
    <CvTitle title="Thông tin liên hệ" />
    <ContactItem src="assets/icons/calendar-green.jpg" info="08/10/1998" />
    <ContactItem src="assets/icons/user-green.jpg" info="Nam" />
    <ContactItem src="assets/icons/phone-green.jpg" info="0914078960" />
    <ContactItem
      src="assets/icons/mail-green.jpg"
      info="khanhk62hust@gmail.com"
    />
    <ContactItem
      src="assets/icons/location-green.jpg"
      info="Ngõ 254D  Minh Khai, Hà Nội"
    />
    <ContactItem
      src="assets/icons/link-green.jpg"
      info="https://www.facebook.com/khanh.lee.3958"
    />
  </View>
);

// Create style with font-family

const styles = StyleSheet.create({
  contactContainer: {
    display: 'flex',
    flexDirection: 'column',
    marginBottom: 10,
  },
  avatarContainer: {
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
  },
  avatar: {
    border: 2,
    width: 120,
    height: 120,
    borderRadius: 120 / 2,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    borderColor: '#1a936f',
  },
  image: {
    width: 110,
    height: 110,
    borderRadius: 55,
  },
  name: {
    fontSize: 24,
    fontFamily: 'Roboto',
    marginVertical: 10,
    color: '#1a936f',
  },
  contactItem: {
    display: 'flex',
    flexDirection: 'row',
    alignItems: 'center',
    marginVertical: 4,
  },
  contactInfo: {
    fontSize: 12,
    marginLeft: 6,
    color: '#666666',
  },
  iconContainer: {
    width: 20,
    height: 20,
    border: 1,
    borderRadius: 15,
    display: 'flex',
    alignItems: 'center',
    justifyContent: 'center',
    borderColor: '#1a936f',
  },
  icon: {
    width: 12,
    height: 12,
  },
});

export default CvProfile;
